import React from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import { WagmiConfig } from 'wagmi';
import { QueryClient, QueryClientProvider } from '@tanstack/react-query';
import { Toaster } from 'react-hot-toast';
import { wagmiConfig } from '@/config/wagmi';
import Layout from '@/components/layout/Layout';
import Dashboard from '@/pages/Dashboard';
import Deposit from '@/pages/Deposit';
import Policies from '@/pages/Policies';
import Audit from '@/pages/Audit';
import Onboarding from '@/pages/Onboarding';
import Prices from '@/pages/Prices';
import Admin from '@/pages/Admin';
import GlobalWalletModal from '@/components/wallet/GlobalWalletModal';
import ErrorBoundary from '@/components/ErrorBoundary';
import '@/styles/globals.css';

const queryClient = new QueryClient({
  defaultOptions: {
    queries: {
      staleTime: 5000,
      gcTime: 1000 * 60 * 5,
      retry: 1,
      refetchOnWindowFocus: false,
      refetchOnMount: false,
      refetchOnReconnect: false,
    },
  },
});

// Clear wagmi cache on every startup to prevent QuotaExceededError
try {
  localStorage.removeItem('wagmi.cache');
  localStorage.removeItem('wagmi.store');
  localStorage.removeItem('wagmi.wallet');
  console.log(' Cleared wagmi cache on startup');
} catch (error) {
  console.warn('Could not clear cache:', error);
  console.warn('Failed to check wagmi cache:', error);
}

const App: React.FC = () => {
  return (
    <ErrorBoundary>
      <QueryClientProvider client={queryClient}>
        <WagmiConfig config={wagmiConfig}>
          <Router>
            <Routes>
              <Route path="/" element={<Layout />}>
                <Route index element={<Navigate to="/dashboard" replace />} />
                <Route path="dashboard" element={<Dashboard />} />
                <Route path="deposit" element={<Deposit />} />
                <Route path="prices" element={<Prices />} />
                <Route path="policies" element={<Policies />} />
                <Route path="audit" element={<Audit />} />
                <Route path="onboarding" element={<Onboarding />} />
                <Route path="admin" element={<Admin />} />
              </Route>
            </Routes>
          </Router>
          <GlobalWalletModal />
          <Toaster
            position="top-right"
            toastOptions={{
              duration: 4000,
              style: {
                background: '#fff',
                color: '#374151',
                boxShadow: '0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05)',
                borderRadius: '12px',
                border: '1px solid #e5e7eb',
                padding: '16px',
              },
              success: {
                iconTheme: {
                  primary: '#10b981',
                  secondary: '#fff',
                },
              },
              error: {
                iconTheme: {
                  primary: '#ef4444',
                  secondary: '#fff',
                },
              },
            }}
          />
        </WagmiConfig>
      </QueryClientProvider>
    </ErrorBoundary>
  );
};

export default App;