/**
 * Hook for fetching prices directly from Pyth Network
 */

import { useState, useEffect, useCallback, useRef } from 'react';
import { pythService, PythPriceFeed } from '../services/pythService';

interface UsePythPricesOptions {
  refreshInterval?: number;
  enabled?: boolean;
}

export const usePythPrices = (symbols: string[], options: UsePythPricesOptions = {}) => {
  const { refreshInterval = 30000, enabled = true } = options;
  const [prices, setPrices] = useState<Map<string, PythPriceFeed>>(new Map());
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const intervalRef = useRef<NodeJS.Timeout>();

  const fetchPrices = useCallback(async () => {
    if (!enabled || symbols.length === 0) return;

    try {
      setIsLoading(true);
      setError(null);
      
      const priceMap = await pythService.getLatestPrices(symbols);
      setPrices(priceMap);
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to fetch prices');
      console.error('Error fetching Pyth prices:', err);
    } finally {
      setIsLoading(false);
    }
  }, [symbols, enabled]);

  // Initial fetch
  useEffect(() => {
    fetchPrices();
  }, [fetchPrices]);

  // Set up refresh interval
  useEffect(() => {
    if (!enabled || refreshInterval <= 0) return;

    intervalRef.current = setInterval(fetchPrices, refreshInterval);

    return () => {
      if (intervalRef.current) {
        clearInterval(intervalRef.current);
      }
    };
  }, [fetchPrices, refreshInterval, enabled]);

  const getPrice = useCallback((symbol: string): PythPriceFeed | undefined => {
    return prices.get(symbol.toUpperCase());
  }, [prices]);

  const refetch = useCallback(() => {
    return fetchPrices();
  }, [fetchPrices]);

  return {
    prices,
    priceMap: prices,
    isLoading,
    error,
    getPrice,
    refetch,
  };
};

/**
 * Hook for fetching a single token price from Pyth
 */
export const usePythPrice = (symbol: string, options: UsePythPricesOptions = {}) => {
  const { refreshInterval = 30000, enabled = true } = options;
  const [price, setPrice] = useState<PythPriceFeed | null>(null);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const intervalRef = useRef<NodeJS.Timeout>();

  const fetchPrice = useCallback(async () => {
    if (!enabled || !symbol) return;

    try {
      setIsLoading(true);
      setError(null);
      
      const priceData = await pythService.getLatestPrice(symbol);
      setPrice(priceData);
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to fetch price');
      console.error(`Error fetching Pyth price for ${symbol}:`, err);
    } finally {
      setIsLoading(false);
    }
  }, [symbol, enabled]);

  // Initial fetch
  useEffect(() => {
    fetchPrice();
  }, [fetchPrice]);

  // Set up refresh interval
  useEffect(() => {
    if (!enabled || refreshInterval <= 0) return;

    intervalRef.current = setInterval(fetchPrice, refreshInterval);

    return () => {
      if (intervalRef.current) {
        clearInterval(intervalRef.current);
      }
    };
  }, [fetchPrice, refreshInterval, enabled]);

  const refetch = useCallback(() => {
    return fetchPrice();
  }, [fetchPrice]);

  return {
    price,
    priceData: price,
    isLoading,
    error,
    refetch,
  };
};

/**
 * Hook for getting supported tokens from Pyth
 */
export const usePythSupportedTokens = () => {
  const [tokens, setTokens] = useState<string[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const fetchTokens = useCallback(async () => {
    try {
      setIsLoading(true);
      setError(null);
      
      const supportedTokens = await pythService.getSupportedTokens();
      setTokens(supportedTokens);
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to fetch supported tokens');
      console.error('Error fetching supported tokens:', err);
    } finally {
      setIsLoading(false);
    }
  }, []);

  useEffect(() => {
    fetchTokens();
  }, [fetchTokens]);

  return {
    tokens,
    isLoading,
    error,
    refetch: fetchTokens,
  };
};

